import React, { Component } from 'react';
import { Panel, Input, Select, Button, Row, Col } from 'react-bootstrap';
import Slider from 'rc-slider';
import { PortSelector } from '../contrib';
import { search } from 'actions';
import store from 'store';

export default class Search extends Component {
  constructor(props) {
    super(props);

    this.state = {
      origin:'',
      destination:'',
      depth:'',
      cost:'',
      term:'',
    }

    this.handleClickSearch = this.handleClickSearch.bind(this);
  }

  handleClickSearch() {
    const origin = this.refs.origin.getValue();
    const destination = this.refs.destination.getValue();
    const depth = this.refs.depth.getValue();
    const cost = this.refs.cost.getValue() * 10000;
    const trem = this.refs.term.getValue();

    store.dispatch(search.routeSearch(origin, destination, depth, cost, term));
  }

  render() {
    const { origin, destination, depth, cost, term } = this.state;

    return (
      <form className="search-inputs">
        <Panel>
          <Row>
            <Col md={6}>
              <div>
                <h3></h3>
                <PortSelector
                  label={' '}
                  ref="origin"
                />
              </div>
            </Col>
            <Col md={6}>
              <div>
                <h3></h3>
                <PortSelector
                  label={' '}
                  ref="destination"
                />
              </div>
            </Col>
          </Row>
        </Panel>
        <Panel className="options">
          <Row>
            <Col md={4}>
              <label>
                  
              </label>
              <Input type="number" ref="depth"/>
            </Col>
            <Col md={4}>
              <label>
                 
              </label>
              <Input type="number" ref="cost"/>
            </Col>
            <Col md={4}>
              <label>
                  
              </label>
              <Slider range allowCross={false} defaultValue={[0, 60]} ref="term"/>
            </Col>
          </Row>
        </Panel>
        <Button bsSize="large" bsStyle="success" onClick={this.handleClickSearch}>
          Search
        </Button>

      </form>
    );
  }
}



